body {
  font-family: 'Inter', sans-serif;
  background-color: #1a1a1a;
  color: #f9f9f9;
  margin: 0;
  padding: 0;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #2c2c2c;
}
.header h1 {
  color: #0098EA;
  margin: 0;
}
.header nav a {
  color: #f9f9f9;
  margin: 0 10px;
  text-decoration: none;
}
.auction-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #333;
  margin-bottom: 10px;
  border-radius: 8px;
}
.auction-item a {
  color: #0098EA;
  text-decoration: none;
}
.auction-item .price {
  color: #00FFFF;
}
